@font-face { font-family: 'NanumBarunpen Bold'; src: url(./fonts/NanumBarunpenB.ttf) format('truetype'); }

.Home {
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;

    scale: 1.0;

    border: 10px solid red;
}

.MainImage { 
    width: 100vw;
}

.StartButton {
    align-self: center;

    position: relative;
    margin-top: -100px;
    width: 50%;

    color: white;
    background: #2e425c;
    padding: 24.3px 59.4px;

    font-family: 'NanumBarunpen Bold';
    line-height: 1.7;

    border: 6.3px solid #95c8e6;
    border-radius: 47.7px;
    box-shadow: 0 0 11.7px 0 #008cff;
    outline: 0;

    

    font-size: 2rem;
}